import React from 'react';
import {Grid,Row,Col, Input, ButtonInput} from 'react-bootstrap';
import action from '../actions/actions.js';
import appStore from '../stores/appStore.js';
import Counter from '../components/r-quality/index.jsx';
import '../components/r-quality/index.less';

function getStateFromStore() {
    return {
        kinds: appStore.getPonyKinds(),
        colors: appStore.getPonyColors()
    }
}

class PonyForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = getStateFromStore();
    }

    componentDidMount() {
        appStore.addChangeListener(this._onChange);
    }

    submit = (e) => {
        e.preventDefault();
        let pony = {
            name: this.refs.name.getValue(),
            age: this.refs.age.getValue(),
            isEvil: this.refs.evil.getInputDOMNode().checked ? true : false,
            color: this.refs.color.getValue(),
            kind : this.refs.kind.getValue(),
            description: this.refs.description.getValue(),
            friends : this.refs.friends.getValue()
        };
        action.addPony(pony);

        console.log(this.refs.friends.getValue());
    };



    _onChange = () => {
        this.setState(getStateFromStore());
        this.refs.name.getInputDOMNode().value = '';
    };


    render() {
        return (
            <form onSubmit={this.submit}>
                <h3> </h3>
                <Input required ref="name" type='text' label='' placeholder=' ' />
                <Input ref="age" type='number' label='' placeholder='   ?' />
                <div>
                    <label className="control-label">    ?</label>
                    <Counter ref="friends" />
                </div>
                <Input value="isEvil" ref="evil" type='checkbox' label='  ?' checked={this.state.isEvil} />
                <Input ref="kind" defaultValue="" type='select' label='' placeholder=' '>
                    {this.state.kinds.map((kind, index) => {
                        return (<option key={index} value={kind}>{kind}</option>);
                    })}
                    <option value=''>...</option>
                </Input>

                <Input ref="color" defaultValue="" type='select' label='   ?' placeholder=''>
                    {this.state.colors.map((color, index) => {
                        return (<option key={index} value={color}>{color}</option>);
                    })}
                    <option value=''>...</option>
                </Input>
                <Input ref="description" type='textarea' label='     ?' placeholder='' />
                <ButtonInput type='submit' value='' />
            </form>
        )
    }
}


export default PonyForm;


