import React from 'react';
import {Grid,Row,Col, Input} from 'react-bootstrap';
import actions from '../actions/actions.js';
import '../css/pony/pony.less';


class PonyItem extends React.Component {
    constructor(props) {
        super(props);
    }

    remove = (e) => {
        actions.remove(this.props.pony);
    };

    render() {
        return (
            <div className="pony">
                <div className="pony__name">{this.props.pony.name}</div>
                <div className="pony__prop">
                    : {this.props.pony.kind ? <b>{this.props.pony.kind}</b> : <i></i>}
                </div>
                <div className="pony__prop">
                    : {this.props.pony.color ? <b>{this.props.pony.color}</b> : <i></i>}
                </div>
                <div className="pony__prop">
                    : {this.props.pony.age ? <b>{this.props.pony.age}</b> : <i></i>}
                </div>
                <div className="pony__prop">
                    : {this.props.pony.friends ? <b>{this.props.pony.friends}</b> : <i></i>}
                </div>
                <div className="pony_prop">
                    {this.props.pony.isEvil ? <b> </b> : <b> </b>}
                </div>
                <div className="pony__description">{this.props.pony.description}</div>

                <div className="pony__remove">
                    <span onClick={this.remove}></span>
                </div>
            </div>
        );
    }
}

export default PonyItem;


